    </keep-alive>
    <!-- poster -->
    <van-overlay
      class="poster"
      v-if="dayCover"
      :show="isshowPoster"
      @click="isshowPoster = false"
      :style="{ 'background-image': 'url(' + dayCover.image_blurred + ')' }"
    >
      <div class="wrapper" @click.stop="isshowPoster = false">
        <van-icon class="close" name="cross" />
        <div class="content">
          <div class="thumb">
            <img :src="dayCover.image" alt />
            <div class="time">
              <span>{{ dayCover.time.day }}</span
              ><span
                >{{ dayCover.time.month }}<br />{{ dayCover.time.week }}</span
              >
            </div>
          </div>
          <div class="bottom">
            <div class="intro">{{ dayCover.title }}</div>
            <div class="share">
              <p>{{ dayCover.content }}</p>
