          </div>
        </div>
      </div>
    </van-overlay>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      isshowPoster: false,
      dayCover: null
    };
  },
  methods: {
    gosearch() {
      this.$router.push("/search");
    },
    showPoster() {
      this.isshowPoster = true;
    },
    getDayCover() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/DayCover/getDayCover"
        )
        .then(response => {
          this.dayCover = response.data.data;
        });
    },
    gotfenxiang() {
      window.console.log("分享");
    }
  },
  created() {
    // window.console.log("信息");
    this.getDayCover();
  }
};
