import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    finditemsdata: null,
    finditemslist: [],
    lists: [],
    isgetdata: false,
    isactive: false,
    contentitem: null,
    clickitem: null,
    daysitemslist: null,
    showitem: false,
    recruitScrollY: 0,
    ishasuser: true,
    user: null,
    comeset: false,
    likeitem: null,
    findsctopY: null,
    keep: false
  },
  mutations: {
    submitisgetdata(state, i) {
      state.isgetdata = i;
    },
    submitcardllist(state, i) {
      state.finditemsdata = i;
      state.lists.push(i.posts.next_page_url_full);
    },
    submitcardpushurl(state, i) {
      state.lists.push(i);
    },
    submitIsactive(state, i) {
      state.isactive = i;
    },
    submitcontentitem(state, i) {
      state.contentitem = i;
    },
    submiTclickitem(state, i) {
      state.clickitem = i;
    },
    submiTdaysitemslist(state, i) {
      state.daysitemslist = i;
    },
    submiShowitem(state, i) {
      state.showitem = i;
    },
    //登录界面的数据提交
    submitIshasuser(state, i) {
      state.ishasuser = i;
    },
    submitUser(state, i) {
      state.user = i;
    },
    submitSet(state, i) {
      state.comeset = i;
    },
    submitLikeItem(state, i) {
      state.likeitem = i;
    },
    submitFindsctopY(state, i) {
      state.findsctopY = i;
    },
    submitKeep(state, i) {
      state.keep = i;
    },
    changeRecruitScrollY(state, recruitScrollY) {
      state.recruitScrollY = recruitScrollY;
    }
  },
  actions: {},
  modules: {},
  getters: {
    recruitScrollY: state => state.recruitScrollY
  }
});
